@mixin main_text_style{
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 130%;
}

.footer{
  width: 100%;
  height: 247px;
  position: relative;
  bottom: 0;
  background: #FFFFFF;
  margin-top: auto;
  display: flex;
}

.leftTitle{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;
  margin-top: 50px;
  margin-left: 280px;
  width: 440px;
}

.date{
  @include main_text_style;
  width: 440px;
  height: 19px;
  color: #000000;
  text-align: left;
}

.rightTitle{
  margin-top: 50px;
  flex-direction: row;
  display: flex;
  margin-left: 135px;
}
.footerLinksBlock{
  width: 210px;
  flex-direction: column;
  display: flex;
  text-align: left;
}
.footerLinksBlock_1{
  @include main_text_style;
  font-weight: 500;
  font-size: 17px;
  margin-bottom: 10px;
}

.links_text{
    @include main_text_style;
    width: auto;
    color: #000000;
    text-decoration: none;
    margin-top: 5px;
    &:hover{
      text-decoration: green underline;
      font-weight: bold;
      color: darkgreen;
    }
}