@mixin main_text_styles{
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  font-weight: 500;
  line-height: 130%;
  position: relative;
}

@mixin gray_text_styles{
  @include main_text_styles;
  font-size: 20px;
  color: #878787;
  height: 23px;
}

.main_content{
  display: flex;
  flex-direction: column;
}
.title{
  display: flex;
  flex-flow: column;
}
.title_block{
  @include gray_text_styles;
  text-align: left;
  margin-top: 30px;
}
.links_text{
  @include main_text_styles;
  font-size: 20px;
  color: #878787;
  width: 222px;
  height: 23px;
  line-height: 20px;
  text-decoration: none;

  &:hover{
    text-decoration: green underline;
    color: darkgreen;
  }
}
//////////////////////////////////////////
.header_controllers{
  display: flex;
  margin-top: 13px;
}
.text_name{
  @include main_text_styles;
  font-size: 42px;
  height: 55px;
}
.result_text{
  display: flex;
  width: 325px;
  height: 60px;
  flex-direction: column;
  text-align: right;
  margin-left: 710px;
}
.header_text_2{
  @include gray_text_styles;
}
.header_text_3{
  @include gray_text_styles;
  color: #000000;
}
//////////////////////////////////////////

.form_block{
  display: flex;
  background: #0F7C10;
  height: 46px;
  flex-direction: row;
  align-items: center;
  width: 1370px;
}

.filter{
  display: flex;
  align-items: center;
}

.sort_text{
  @include main_text_styles;
  color: white;
  font-size: 15px;
  margin-left: 710px;
  margin-right: 8px;
}

.selector_text{
  @include main_text_styles;
  color: white;
  font-size: 17px;
  font-weight: bold;
}
.table_enable{
  fill: white;
  margin-left: 64px;
}

.table_disable{
  fill: #0E5B0F;
  margin-left: 62px;
}

.row_enable{
  fill: white;
  margin-left: 10px;
}
.row_disable{
  fill: #0E5B0F;
  margin-left: 10px;
}

.price_range{
  @include main_text_styles;
  display: flex;
  color: #F9F9F9;
  font-size: 17px;
  width: 127px;
  margin-left: 20px;
}
.input_style{
  position: relative;
  display: inline-flex;
}

input[type='range'] {
  overflow: hidden;
  width: 217px;
  -webkit-appearance: none;
  background: #0F7C10;
}

input[type='range']::-webkit-slider-runnable-track {
  height: 10px;
  -webkit-appearance: none;
  background: #0F7C10;
  margin-top: -1px;
}
input[type='range']::-webkit-slider-thumb {
  width: 10px;
  -webkit-appearance: none;
  height: 10px;
  cursor: ew-resize;
  background: #0F7C10;
  box-shadow: -80px 0 0 80px #FFFFFF;
}
input[type="range"]::-moz-range-progress {
  background-color: #FFFFFF;
}
input[type="range"]::-moz-range-track {
  background-color: #FFFFFF;
}

