.header{
  display: flex;
  justify-content: space-between;
  position: fixed;
  width: 100%;
  height: 100px;
  right: 0;
  top: 0;
  bottom: 0;
  background: #FFFFFF;
  z-index: 100;
}
.searchBlock{
  width: 103px;
  height: 40px;
  position: absolute;
  left: 14.58%;
  right: 80.05%;
  top: 25%;
  bottom: 30%;

  .search_svg{
    box-sizing: border-box;
    position: absolute;
    right: 83%;
    top: 38%;
    bottom: 30%;
  }

  .searchText{
    width: auto;
    height: 20px;
    position: absolute;
    left: 17.19%;
    right: 80.05%;
    top: 40%;
    bottom: 40%;
    font-family: 'Roboto', sans-serif;
    font-style: normal;
    font-size: 17px;
    line-height: 20px;
    color: #000000;

    &:hover{
      text-decoration: green underline;
      font-weight: bold;
    }
  }

}
